import React, { useState, useEffect } from 'react';
import { BeakerIcon, PlayIcon, ChatBubbleLeftRightIcon } from '@heroicons/react/24/outline';
import TestCaseList from './TestCaseList';
import ChatPanel from './ChatPanel';
import StatusBar from './StatusBar';
import AutoDetectToggle from './AutoDetectToggle';

// Get vscode API
declare global {
    interface Window {
        acquireVsCodeApi(): any;
    }
}

const vscode = window.acquireVsCodeApi();

interface TestCase {
    id: string;
    title: string;
    code: string;
    status: 'pending' | 'passed' | 'failed';
    error?: string;
}

const App: React.FC = () => {
    const [activeTab, setActiveTab] = useState<'tests' | 'chat'>('tests');
    const [tests, setTests] = useState<TestCase[]>([]);
    const [messages, setMessages] = useState<{ text: string; isUser: boolean }[]>([]);
    const [status, setStatus] = useState({ message: '', loading: false });
    const [autoDetectEnabled, setAutoDetectEnabled] = useState(false);
    const [currentFile, setCurrentFile] = useState('');

    useEffect(() => {
        const handleMessage = (event: MessageEvent) => {
            const message = event.data;
            switch (message.command) {
                case 'updateTests':
                    setTests(message.tests);
                    break;
                case 'addMessage':
                    setMessages(prev => [...prev, { text: message.text, isUser: message.isUser }]);
                    break;
                case 'updateStatus':
                    setStatus({ message: message.message, loading: message.loading });
                    break;
                case 'updateFile':
                    setCurrentFile(message.filePath);
                    break;
            }
        };

        window.addEventListener('message', handleMessage);
        return () => window.removeEventListener('message', handleMessage);
    }, []);

    const handleGenerateTests = () => {
        vscode.postMessage({ command: 'generateTests' });
    };

    const handleRunTests = () => {
        vscode.postMessage({ command: 'runAllTests' });
    };

    const handleSendMessage = (text: string) => {
        vscode.postMessage({ command: 'sendMessage', text });
        setMessages(prev => [...prev, { text, isUser: true }]);
    };

    const handleAutoDetectToggle = (enabled: boolean) => {
        setAutoDetectEnabled(enabled);
        vscode.postMessage({ command: 'toggleAutoDetect', enabled });
    };

    return (
        <div className="h-screen flex flex-col bg-vscode-bg text-vscode-fg">
            <div className="flex items-center justify-between p-4 border-b border-vscode-border">
                <div className="flex space-x-4">
                    <button
                        className={`px-4 py-2 rounded-lg flex items-center gap-2 ${
                            activeTab === 'tests' ? 'bg-vscode-button text-vscode-button-fg' : 'hover:bg-vscode-button/10'
                        }`}
                        onClick={() => setActiveTab('tests')}
                    >
                        <BeakerIcon className="w-5 h-5" />
                        Test Cases
                    </button>
                    <button
                        className={`px-4 py-2 rounded-lg flex items-center gap-2 ${
                            activeTab === 'chat' ? 'bg-vscode-button text-vscode-button-fg' : 'hover:bg-vscode-button/10'
                        }`}
                        onClick={() => setActiveTab('chat')}
                    >
                        <ChatBubbleLeftRightIcon className="w-5 h-5" />
                        AI Chat
                    </button>
                </div>
            </div>

            <StatusBar message={status.message} loading={status.loading} currentFile={currentFile} />

            {activeTab === 'tests' && (
                <div className="flex-1 overflow-hidden flex flex-col">
                    <div className="p-4 flex items-center justify-between">
                        <div className="flex gap-2">
                            <button
                                className="px-4 py-2 bg-vscode-button text-vscode-button-fg rounded-lg flex items-center gap-2 hover:bg-vscode-button-hover"
                                onClick={handleGenerateTests}
                            >
                                <BeakerIcon className="w-5 h-5" />
                                Generate Tests
                            </button>
                            <button
                                className="px-4 py-2 bg-vscode-button text-vscode-button-fg rounded-lg flex items-center gap-2 hover:bg-vscode-button-hover"
                                onClick={handleRunTests}
                            >
                                <PlayIcon className="w-5 h-5" />
                                Run Tests
                            </button>
                        </div>
                        <AutoDetectToggle enabled={autoDetectEnabled} onToggle={handleAutoDetectToggle} />
                    </div>
                    <TestCaseList tests={tests} />
                </div>
            )}

            {activeTab === 'chat' && (
                <ChatPanel messages={messages} onSendMessage={handleSendMessage} />
            )}
        </div>
    );
};

export default App;
