import React from 'react';
import { CheckCircleIcon, XCircleIcon, ClockIcon } from '@heroicons/react/24/outline';
import classNames from 'classnames';

interface TestCase {
    id: string;
    title: string;
    code: string;
    status: 'pending' | 'passed' | 'failed';
    error?: string;
}

interface TestCaseListProps {
    tests: TestCase[];
}

const TestCaseList: React.FC<TestCaseListProps> = ({ tests }) => {
    const getStatusIcon = (status: string) => {
        switch (status) {
            case 'passed':
                return <CheckCircleIcon className="w-5 h-5 text-green-500" />;
            case 'failed':
                return <XCircleIcon className="w-5 h-5 text-red-500" />;
            default:
                return <ClockIcon className="w-5 h-5 text-yellow-500" />;
        }
    };

    return (
        <div className="flex-1 overflow-y-auto p-4">
            <div className="space-y-4">
                {tests.map((test) => (
                    <div
                        key={test.id}
                        className="bg-vscode-input-bg rounded-lg p-4 border border-vscode-border"
                    >
                        <div className="flex items-center justify-between mb-3">
                            <h3 className="text-sm font-medium flex items-center gap-2">
                                {getStatusIcon(test.status)}
                                {test.title}
                            </h3>
                            <span
                                className={classNames(
                                    'px-2 py-1 rounded-full text-xs font-medium',
                                    {
                                        'bg-green-100 text-green-800': test.status === 'passed',
                                        'bg-red-100 text-red-800': test.status === 'failed',
                                        'bg-yellow-100 text-yellow-800': test.status === 'pending',
                                    }
                                )}
                            >
                                {test.status}
                            </span>
                        </div>
                        <pre className="bg-[color:var(--vscode-editor-background)] p-3 rounded text-sm font-mono overflow-x-auto">
                            {test.code}
                        </pre>
                        {test.error && (
                            <div className="mt-3 p-3 bg-red-100 text-red-800 rounded-md text-sm">
                                {test.error}
                            </div>
                        )}
                    </div>
                ))}
                {tests.length === 0 && (
                    <div className="text-center py-8 text-vscode-fg/60">
                        No test cases generated yet. Click "Generate Tests" to create test cases for your code.
                    </div>
                )}
            </div>
        </div>
    );
};

export default TestCaseList;
